import React from 'react';

function UpvoteSection({articleName, upvotes, setArticleInfo}) {

    const addUpvotes = async () => {
        let result = await fetch(`/api/article/${articleName}/upvote`, {
            method: 'post'
        });
        let body = await result.json();
        setArticleInfo(body);
    }

    return(
        <div id="upvotes-section">
            <button onClick={() => addUpvotes()}>Add Upvotes</button>
            <p>This article has been upvoted {upvotes} times</p>
        </div>
    );
}

export default UpvoteSection;