import React, {useState, useEffect} from 'react';
import articles from './articleContent';
import ArticlesList from '../ArticlesList';
import UpvoteSection from '../UpvotesSection';
import NotFoundPage from './NotFoundPage';
import CommentsList from '../CommentsList';
import AddCommentForm from '../AddCommentForm';

const ArticlePage = ({match}) => {

    const name = match.params.name;
    const article = articles.find(art => art.name === name);
    const [articleInfo, setArticleInfo] = useState({upvotes:0, comments:[]});

    useEffect(() => {
        const fetchData = async () => {
            let result = await fetch(`/api/article/${name}`);
            let body = await result.json();
            setArticleInfo(body);
        }
        fetchData();
    },[name]);

    const otherArticles = articles.filter(art => art.name !== name);

    if(!article) return <NotFoundPage />

    return (
        <>
            <h1>{article.name}</h1>
            <UpvoteSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo}/>
            {article.content.map((paragraph, i) => <p key={i}>{paragraph}</p>)}
            <CommentsList comments={articleInfo.comments}/>
            <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
            <h3>Other Articles: </h3>
            <ArticlesList articles={otherArticles} />
        </>
    );
};

export default ArticlePage;