import React from 'react';

const HomePage = () => {
    return (
        <>
            <h1>Hello, welcome to my blog!!!</h1>
            <p>
                Welcome to my blog, this is just a sample blog. Before applying the Service, User has to register using the Citizen Registration Link given on login Screen. Already Registered Citizen can avail the Service Using Login ID and Password generated after Citizen Registration.
            </p>
            <p>
                Test project for React js. Applicant has to upload the Passport size scan photo (jpg image) in the clear background of size less than 50 kb.
            </p>
            <p>
                Building this project to get more knowledge about React. After Login, Applicant has to click on the e-services tab in the left menu and thereafter click on the department to which he has to avail the service.(e.g. Scholarship schemes under Social Welfare Department or Domicile Service Using General Administration Dept.)
            </p>
        </>
    );
}

export default HomePage;